<template>
    <div class="yes24">
        <div class="top ">
            <div class="tit-area">
                <span class="logo">
                    <span class="blind">책읽아웃 로고</span>
                </span>
                <div class="tit-txt">
                    <h1>책읽아웃</h1>
                    <p class="author">YES24</p>
                </div>
            </div>
            <div class="season-ctrl">
                <ul class="link-list">
                    <li>
                        <a href="https://www.podbbang.com/channels/15135" target="_blank" title="새창 열림"><span class="icon is-small"><i class="mdi mdi-podcast"></i></span>팟빵</a>
                    </li>
                    <li>
                        <a href="https://podcasts.apple.com/kr/podcast/%EC%B1%85%EC%9D%BD%EC%95%84%EC%9B%83/id1298978980" target="_blank" title="새창 열림"><span class="icon is-small"><i class="mdi mdi-podcast"></i></span>팟캐스트</a>
                    </li>
                    <li>
                        <a href="https://audioclip.naver.com/channels/391" target="_blank" title="새창 열림"><span class="icon is-small"><i class="mdi mdi-podcast"></i></span>네이버 오디오 클립</a>
                    </li>
                </ul>
            </div> 
        </div>   
        <div class="form-ctrl">
            <div class="ctrl-btn">
                <button type="button" class="button is-primary" @click="sortCtrl">
                    <span class="icon is-small"><i class="mdi mdi-sort"></i></span>  
                    <span class="tx">{{ sortTx }}</span>  
                </button>
                <div v-if="(sortCate == '삼천포책방' || sortCate == '어떤,책임' || sortCate == '책책책' || sortCate == '이주의 도서')" class="listtype-btns">
                    <span class="bar"></span>
                    <button type="button" class="button is-primary" data-type="episode" @click="setListType($event)">
                        <span class="icon is-small"><i class="mdi mdi-cast-audio"></i></span>  
                        <span class="tx">에피소드별</span>  
                    </button>
                    <button type="button" class="button is-primary" data-type="book" @click="setListType($event)">
                        <span class="icon is-small"><i class="mdi mdi-book-open-variant"></i></span>  
                        <span class="tx">도서별</span>  
                    </button>
                    <button type="button" class="button is-primary" data-type="reviewer" @click="setListType($event)">
                        <span class="icon is-small"><i class="mdi mdi-microphone"></i></span>  
                        <span class="tx">소개자별</span>  
                    </button>
                </div>
                <span class="bar"></span>
                <button type="button" class="button is-primary" id="btnFilter" @click="setFilterActive">
                    <span class="icon is-small"><i class="mdi" v-bind:class="[filterActive ? 'mdi-filter' : 'mdi-filter-outline']"></i></span>  
                    <span class="blind">필터</span>  
                </button>
            </div>
            <div class="input-search">
                <select id="searchOption" @change="setSearchOption($event)">
                    <option value="episodeTit">에피소드 제목</option>
                    <option value="bookTitle">책 제목</option>
                    <option value="author">저자</option>
                    <!-- <option value="name">소개하는 사람</option> -->
                </select>
                <input type="text" v-model="search">
            </div>
        </div>
        <div class="list">
            <h2 class="blind">아카이빙 리스트</h2>
            <div class="sort-category pc">
                <button type="button" class="button" @click="sortCategory($event)" v-for="(btn, idx) in sortCategories" :key="idx" :class="{'active' : idx === 0 }">{{btn}}</button>
            </div>

            <div class="sort-category mobile">
                <select name="" id="selectCate" @change="sortCategory($event)">
                    <option :value="'sort'+idx" v-for="(option, idx) in sortCategories" :key="idx">{{ option }}</option>
                </select>
            </div>
            <div class="card-wrap" v-if="this.listType == 'episode'">
                <div class="view-item episode-item" v-for="(item, idx) in filteredData" :key="idx" :id="'item' + idx">
                    <div class="collapse card">
                        <div class="collapse-trigger" role="button" @click="accordion($event)">
                            <div class="card-header">
                                <div class="card-header-title">
                                    <div class="item-tit">
                                        <span class="epi-num">{{ item.episodeNum }}</span>
                                        <span class="category">[{{ item.category }}]</span>
                                        <h2 class="epi-tit">{{ item.episodeTit }}</h2>
                                    </div>    
                                    <a :href="'https://www.podbbang.com/channels/15135/episodes/'+item.episodeLink" target="_blank" title="새창 열림"  v-if="item.episodeLink" class="podcast">
                                        <span class="icon is-medium"><i class="mdi mdi-podcast"></i></span>
                                    </a>
                                </div>
                                <div class="card-header-icon">
                                    <span class="icon">
                                        <i class="mdi mdi-menu-down mdi-24px"></i>
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div class="collapse-content"  style="display:none">
                            <div class="card-content">
                                <div class="content">
                                    <ul class="book-list">
                                        <li v-for="(book, idx) in item.books" :key="idx">
                                            <div class="item-tit">
                                                <span class="reviewer" v-if="book.name" >{{ book.name}}</span>
                                                <h3 class="book-tit">{{ book.bookTitle}}</h3>
                                                <span class="author" v-if="book.author"> | {{book.author}}
                                                </span>
                                            </div>
                                            <a :href="book.booklink" target="_blank" title="새창 열림" class="link">{{ book.booklink}}</a>
                                            <ul class="keywords-list" v-if="book.tags">
                                                <li v-for="(tag, j) in book.tags" :key="j" role="button" >{{ tag }}</li>
                                            </ul>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card-wrap"  v-if="this.listType == 'book'" >
                <div class="view-item book-item" v-for="(item, idx) in filteredData" :key="idx" :id="'item' + idx">
                    <div class="collapse card" v-for="(book, i) in item.books" :key="i">
                        <div class="collapse-trigger" role="button" @click="accordion($event)">
                            <div class="card-header">
                                <div class="card-header-title">
                                        <p class="episode"><span class="num">{{ item.episodeNum }}</span>
                                        <span class="category">[{{ item.category }}]</span>
                                        <span class="epi-tit">{{ item.episodeTit }}</span></p>
                                        <div class="item-tit">
                                            <span class="reviewer">{{ book.name}}</span>
                                            <h2 class="book-tit">{{ book.bookTitle}}</h2>
                                            <span class="author"> | {{book.author}}
                                                </span>
                                            </div>
                                    <a :href="item.episodeLink" target="_blank" title="새창 열림"  v-if="item.episodeLink" class="podcast">
                                        <span class="icon is-medium"><i class="mdi mdi-podcast"></i></span>
                                    </a>
                                </div>
                                <div class="card-header-icon">
                                    <span class="icon">
                                        <i class="mdi mdi-menu-down mdi-24px"></i>
                                    </span>
                                </div>
                            </div>
                        </div>
                        <div class="collapse-content"  style="display:none">
                            <div class="card-content">
                                <div class="content">
                                    <div class="main-link">
                                        <p>링크</p>
                                        <a :href="book.booklink" target="_blank" title="새창 열림">
                                            {{ book.booklink }}</a>
                                    </div>
                                    <ul class="keywords-list">
                                        <li v-for="(tag, j) in book.tags" :key="j" role="button" @click="getKeyword($event)">{{ tag }}</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="card-wrap" v-if="this.listType == 'reviewer'">
                <div class="view-item book-item" v-for="(nameArray, i) in reviewersName" :key="i" >
                    <div v-if="i == setCateIndex(i)">
                        <ul class="reviewer-list">
                            <li v-for="(name, j) in nameArray" :key="j" :class="{'active' : j === 0 }" @click="setActiveClass($event)"><a role="button" @click="this.reviewer=name">{{ name }}</a></li>
                        </ul>
                        <div v-for="(item, k) in reviewerList(this.reviewer)" :key="k">
                            <div class="collapse card">
                                <div class="collapse-trigger" role="button" @click="accordion($event)">
                                    <div class="card-header">
                                        <div class="card-header-title"> 
                                            <div class="item-tit">
                                                <h2 class="book-tit">{{ item.bookTitle}}</h2>
                                                <span class="author" v-if="item.author"> | {{item.author}}
                                                    </span>
                                                </div>
                                        </div>
                                        <div class="card-header-icon">
                                            <span class="icon">
                                                <i class="mdi mdi-menu-down mdi-24px"></i>
                                            </span>
                                        </div>
                                    </div>
                                </div>
                                <div class="collapse-content"  style="display:none">
                                    <div class="card-content">
                                        <div class="content">
                                            <div class="main-link" v-if="item.booklink">
                                                <p>링크</p>
                                                <a :href="item.booklink" target="_blank" title="새창 열림">
                                                    {{ item.booklink }}</a>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>   
                        </div>
                    </div> 
                </div>
            </div>
        </div>
    </div> 
    <!-- <input type="file" @change="readFile" > -->
</template>

<script src="../assets/js/yes24.js"></script>