
import listdata from './yes24Data.js';
import XLSX from 'xlsx';

export default {
    data() {
        return {
            episodes : [],
            episodesLength : 0,
            search: '',
            searchedData: [],
            sortBy: 'newest',
            sortTx : '최신회부터',
            sortCate: '전체',
            lastItemId: 0,
            selectedKeyword: '',
            filterActive: false,
            filteredList: [],
            filterClass: 'mdi-filter-outline',
            filterActiveClass: 'mdi-filter',
            sortCategories : ['전체', '측면돌파', '삼천포책방', '옹기종기', '어떤,책임','읽는인간','책책책', '이주의 도서'],
            targetIndex: 0,
            cardData : [],
            listType: 'episode',
            searchOption: 'episodeTit',
            reviewer:'',
            reviewerCategory: ['삼천포책방', '어떤,책임', '책책책'],
            reviewersName : [['톨콩', '그냥', '단호박'], ['불현듯', '프랑소와 엄', '캘리'], ['생선', '프랑소와 엄', '캘리']],
            originalData: [],
        }

    },
    created() {
        this.episodes = listdata;
        this.episodesLength = listdata.length;
        this.lastItemId = this.episodes[this.episodes.length - 1].id;
    },
    mounted() {

    },
    computed: {
        filteredData() {
            this.filteredList = this.episodes;
            if (this.search != '' && this.search) {
                if(this.searchOption !== 'episodeTit'){
                    this.searchedData = this.filteredList.filter((data) => {
                        return data.books.some((book) => {
                            return book[this.searchOption].toLowerCase().includes(this.search.toLowerCase());
                        })
                    })
                    this.searchedData.forEach((a) => {
                        this.cardData = a.books.filter((b) => {
                            return b[this.searchOption].toLowerCase().includes(this.search.toLowerCase()) 
                        })
                        a.books = this.cardData
                    })
                    
                }else {
                    this.searchedData = this.filteredList.filter((data) => {
                        return data.episodeTit.toLowerCase().includes(this.search.toLowerCase());
                    })
                    
                }
                this.filteredList = this.searchedData;
            }
           
            this.filteredList = this.filteredList.sort((a, b) => {
                if(this.sortBy == 'oldest') {
                    return a.id -  b.id;  
                } else if (this.sortBy == 'newest') {
                    return b.id - a.id;
                }
            })

            if (this.sortCate != '전체') {
                this.filteredList = this.filteredList.filter(
                (data) => {
                    return data.category.includes(this.sortCate);
                });
            } 
                
            if (this.selectedKeyword != '' && this.selectedKeyword) {
                this.filteredList = this.filteredList.filter((data) => {
                    return data.books.some((book) => {
                        return book.tags.some((tag) => {
                            return tag.includes(this.selectedKeyword);
                        })
                    })
                })
                this.filteredList.forEach((a) => {
                    this.cardData = a.books.filter((b) => {
                        return b.tags.some((tag) => {
                            return tag.includes(this.selectedKeyword) 
                        })  
                    })
                    a.books = this.cardData
                })
            }

            if(this.filteredList.length !== this.episodesLength) {
                this.filterActive = true;
            }
            this.originalData = this.filteredList
            return this.filteredList
        },

        
    },
    methods: {
        sortCtrl() {
            if(this.sortBy == 'oldest') {
                this.sortBy = 'newest'
                this.sortTx = '최신회부터'
            }else {
                this.sortBy = 'oldest'
                this.sortTx = '첫회부터'
            }
        },
        accordion(event) {
            let el = event.currentTarget;
            let target = el.nextElementSibling;
            if(target.style.display == 'none') {
                target.style.display = 'block';
                el.querySelector('.mdi-menu-down').classList.replace('mdi-menu-down', 'mdi-menu-up');
            }else {
                target.style.display = 'none'
                el.querySelector('.mdi-menu-up').classList.replace('mdi-menu-up', 'mdi-menu-down');
            }
        }, 
        setFilterActive() {
            this.sortCate = '전체';
            this.selectedKeyword = '';
            this.search = '';
            this.filteredList = this.episodes;
            this.filterActive = false;
            this.reviewer = '';
            document.querySelector('.sort-category').children.forEach(a => {
                a.classList.remove('active')
            })
            document.querySelector('.sort-category').firstElementChild.classList.add('active');
            
        },
        sortCategory(event) {
            let el = event.currentTarget;
            let value;
            let text;
            
            el.parentElement.children.forEach(a => {
                a.classList.remove('active')
            })
            el.classList.add('active');

            if(el.id == 'selectCate') {
                value = el.value;
                if(value == 'sort0') {
                    text = '전체'
                }else if(value == 'sort1') {
                    text = '측면돌파'
                }else if (value == 'sort2') {
                    text = '삼천포책방'
                }else if (value == 'sort3') {
                    text = '옹기종기'
                }else if (value == 'sort4') {
                    text = '어떤,책임'
                }else if (value == 'sort5') {
                    text = '읽는인간'
                }else if (value == 'sort6') {
                    text = '책책책'
                }else if (value == 'sort7') {
                    text = '이주의 도서'
                }
            }else {
                text = el.innerHTML;
            }
            
            this.listType = 'episode'
            this.sortCate = text;
            if(this.sortCate == '삼천포책방') {
                this.reviewer = '톨콩'

            }else if (this.sortCate == '어떤,책임') {
                this.reviewer = '불현듯'
            }else if (this.sortCate == '책책책') {
                this.reviewer = '생선'
            }
        },

        setListType(event) {
            let el = event.currentTarget;
            let type = el.getAttribute('data-type')
            this.listType = type;
        },

        setSearchOption(event){
            let el = event.currentTarget;
            let value = el.value
            this.searchOption = value
        },
        reviewerList(review) {
            let reviewBookData = this.originalData;
            let dataArray;
            dataArray = reviewBookData.map(a =>{
                return a.books
            })
            dataArray = dataArray.flat();
            dataArray = dataArray.filter((d) => {
                return d.name.includes(review)
            })

            return dataArray
        },
        setCateIndex(idx) {
            if(this.sortCate == '삼천포책방') {
                idx = 0
            }else if(this.sortCate == '어떤,책임'){
                idx = 1
            }else if (this.sortCate == '책책책') {
                idx = 2
            }
            return idx
        },
        setActiveClass(event) {
            let el = event.currentTarget;
            el.parentElement.children.forEach(a => {
                a.classList.remove('active')
            })
            el.classList.add('active');
        },
        readFile(event) {
            let file = event.target.files ? event.target.files[0] : null;
            //const fileName = file.name;
           
            const reader = new FileReader(); 
            let tmpResult = {};


            reader.onload = (e) => { 
                let data = e.target.result; 
                data = new Uint8Array(data); 
                // get excel file 
                
                let excelFile = XLSX.read(data, { type: "array" }); 
                
                // get prased object 
                excelFile.SheetNames.forEach(function(sheetName) { 
                    const roa = XLSX.utils.sheet_to_json(
                         excelFile.Sheets[sheetName], 
                         { header: 1 } 
                    ); 
                    if (roa.length) tmpResult[sheetName] = roa; 
                }); 
                this.result = tmpResult.Sheet1; 
            }; 
            reader.readAsArrayBuffer(file);
        }
    },
    components: {
        XLSX
    }

}